<template>
  <div class="main-box" :class="{active: isActive}">
    <div class="header-box">
      <headerBox>
        <div slot="left-box" class="logo"></div>
        <header-center slot="center-box"></header-center>
        <header-right slot="right-box"></header-right>
      </headerBox>
    </div>
    <div class="left">
      <left-box>
        <left-search slot="main-left-search" @changeClose="close"></left-search>
        <left-tree slot="main-left-tree" @getLocation="location"></left-tree>
      </left-box>
    </div>
    <div class="right">
      <router-view v-if="isRouterAlive"></router-view>
    </div>
    <div class="bottom"></div>
    <message-content ref="changeLocation"></message-content>
  </div>
</template>

<script>
import headerBox from "./mainHeader/headerBox";
import headerCenter from "./mainHeader/headerCenter";
import headerRight from "./mainHeader/headerRight";

import leftBox from "./mainLeft/leftBox";
import leftTree from "./mainLeft/leftTree";
import leftSearch from "./mainLeft/leftSearch";

import messageContent from "views/msgContent";
import getAxios from "network/api";

export default {
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isActive: false,
      isRouterAlive: true
    };
  },
  components: {
    headerBox,
    headerCenter,
    headerRight,
    leftBox,
    leftSearch,
    leftTree,
    messageContent
  },
  methods: {
    close(tag) {
      // 左侧按钮收缩和展开
      tag ? (this.isActive = true) : (this.isActive = false);
    },
    location(location) {
      this.$refs.changeLocation.isShow = true;

      // this.$refs.changeLocation.style.left = location[0]
    },
    reload() {
      this.isRouterAlive = false
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  }
};
</script>

<style scoped lang="scss">
.main-box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  // 顶部导航
  .header-box {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 90px;
    background-color: #001b24;
    z-index: 999;

    .logo {
      width: 629px;
      height: 61px;
      background: url(/img/title.13bc503e.png) 0 0 no-repeat;
      background-size: 100% 100%;
      -webkit-transform: scale(0.8);
      margin-top: 14px;
      margin-left: -20px;
      transform: scale(0.8);
    }
  }

  // 左侧树
  .left {
    position: absolute;
    left: 0;
    width: 260px;
    top: 90px;
    bottom: 0;
    background-color: #f0f8fc;
    box-shadow: 0 0 3px rgba(51, 51, 51, 0.3);
    overflow: hidden;
    transition: width ease 0.5s;
  }

  // 右侧表格或者地图
  .right {
    position: absolute;
    left: 260px;
    top: 90px;
    bottom: 0;
    right: 0;
    transition: left ease 0.5s;
  }
}
.active {
  .left {
    width: 54px;
  }
  .right {
    left: 54px;
  }
  .left-tree-box {
    animation: fadeOut 0.5s ease;
    animation-fill-mode: forwards;
  }
  @keyframes fadeOut {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }
}
@media screen and (max-width: 920px) and (min-width: 0px){
    .main-box .header-box .logo{
       margin-left: -50px;
       
    }
   }
</style>