<template>
  <div id="header-right">
    <div class="time">
      <div class="time-main" @click="getCurrTime()">{{year}}<br>{{time}}</div>
    </div>
    <div class="return" @click="goBack()">
      <div class="return-img"></div>
      <div class="return-text">退出</div>
    </div>
  </div>
</template>

<script>
import { getTime } from 'network/mainRuequest1'

export default {
  name: 'header-right',
  data(){
    return {
      year: "",
      time: ""
    }
  },
  created: function() {
    window.setInterval(()=>{
      setTimeout(this.getCurrTime(),0)
    },1000)

  
  },
  methods: {
    goBack(){
      this.$router.replace({path: '/login'});
    },
    getCurrTime(){
      getTime()
       .then(res=>{
         this.year = res.data.date;
         this.time = res.data.time;

       })
      .catch(err => console.log(err));
    },
  }
}
</script>

<style scoped lang="scss">
  .time{
    width: 215px;
    height: 75px;
    background: url(~assets/img/main/time-box.png) 0 0 no-repeat;
    background-size: 100% 100%;
    margin-top: 7px;
    font-size: 24px;
    color: #00fff6;
    line-height: 28px;
    text-align: center;
    // margin-left: 64px;

    .time-main{
      padding-top: 10px;
    }
  }
  .return{
    position: absolute;
    right: 27px;
    top: 14px;
    cursor: pointer;

    .return-img{
      width: 38px;
      height: 38px;
      background: url(~assets/img/main/return.png) 0 0 no-repeat;
      background-size: 100% 100%;
    }
    .return-text{
      color: #95d2e3;
      font-size: 16px;
      line-height: 30px;
      text-align: center;
    }
  }
   @media screen and (max-width: 1180px) and (min-width: 0px){
    .time{
       display: none
    }
   }
</style>