<template>
  <div id="left-tree-box" class="left-tree-box" >
    <el-tree :data="data" show-checkbox node-key="id" default-expand-all :props="defaultProps" empty-text=""  @node-contextmenu="rightClick">
      <span slot-scope="{ node, data }">
        <i :class="data.icon"></i>
        <span style="padding-left: 4px;">{{node.label}}</span>
      </span>
    </el-tree>
  </div>
</template>

<script>
import { getTreeList } from 'network/mainRuequest'
import { Loading } from 'element-ui';

export default {
  name: "left-tree-box",
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label"
      },
      isLoading: true
    };
  },
  mounted() {
    this.treeListData();
  },
  methods: {
    treeListData() {
  
      getTreeList()
        .then(res => {
          let data = res.data;
          let list = [];
          data = data.map(res => {
       
            return {
              icon: "el-icon-s-platform",
              iconClose: res.iconClose,
              iconOpen: res.iconOpen,
              id: res.id,
              label: res.name,
              pId: res.pId,
              parent: res.parent,
              sonNodesSum: res.sonNodesSum
            };
          });

          for (var i = 0; i < data.length; i++) {
            for (var j = 0; j < data.length; j++) {
              if (data[i].pId === data[j].id) {
                if (!data[j].children) {
                  data[j].children = [];
                }
                data[j].icon = "el-icon-s-home";
                data[j].children.push(data[i]);
              }
            }
          }

          for (var i = 0; i < data.length; i++) {
            if (data[i].pId === "0") {
              list.push(data[i]);
            }
          }

          this.data = list;
        })
        .catch(err => console.log("err"));
    },
    rightClick($event){
      // 右键弹出框的位置
      // const location = [$event.pageX, $event.pageY];
      // this.$emit("getLocation", location);
    }
  }
};
</script>

<style scoped lang="scss">
.left-tree-box {
  position: absolute;
  padding-right: 20px;
  font-size: 14px;
}
/deep/ .el-tree-node__content:hover {
  background-color: #e5f4fa;
}
</style>